.Order {
    width: 100%;
    border: 1px solid #eee;
    box-shadow: 0 2px 3px #ccc;
    padding: 10px;
    margin: 10px auto;
    box-sizing: border-box;

    .Ingredient {
        display: inline-block;
        text-transform: capitalize;
        margin: 0 8px;
        border: 1px solid #ccc;
        padding: 5px;
    }
}